@import '../../_variables';

.group {
    width: 100%;
    height: 37px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-size: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    &::-webkit-input-placeholder {
        color: $primary-color;
    }
    &:-ms-input-placeholder {
        color: $primary-color;
    }
    &::placeholder {
        color: $primary-color;
    }
}


